import React, { useState, useEffect } from "react";
import "../App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import {
  Button,
  Card,
  CardContent,
  Input,
  CardActions,
} from "@material-ui/core";
function Timer(props) {
  const [ActiveNumber, activesetNumber] = useState([]);
  const [Number, setNumber] = useState([]);
  const [StorageNumber, setStorageNumber] = useState([]);
  const [Second, setSecond] = useState([]);
  const [CountStart, setCountStart] = useState(true);
  const [ButtonClicked, setButtonClicked] = useState(false);
  const [ActiveSecond, setActiveSecond] = useState(60);
  const [Background, setBackground] = useState("bg1");
  const [StopCounter, setStopCounter] = useState(0);
  const [StartBreak, setStartBreak] = useState(true);
  const [isLongBreak, setislongbreak] = useState(false);
  const [isShortBreak, set覺sshortbreak] = useState(false);
  const [isFinish, setisFinished] = useState(false);
  const [DataOfPomodoro, setDataOfPomodoro] = useState([{}]);
  const gettime = (event) => {
    const inputvalue = event.target.value;

    var res = inputvalue.replace(":", ".");

    let convertnumber = parseFloat(res).toFixed(2);
    if (convertnumber == 0.0) {
    } else {
      activesetNumber(convertnumber);
    }
  };
  useEffect(() => {
    setDataOfPomodoro(props.dataofpomodoro);
  }, []);
  useEffect(() => {
    if (ActiveNumber == "NaN") {
      activesetNumber([]);
    }
  }, [ActiveNumber]);

  useEffect(() => {
    if (ButtonClicked) {
      if (CountStart && DataOfPomodoro[0].session != -3) {
        const secondtimer = setTimeout(() => {
          if (CountStart) {
            let activesecond = Second;
            activesecond -= 1;
            setSecond(activesecond);
          }
        }, 1000);
        return () => clearTimeout(secondtimer);
      } else {
        const secondtimer = setTimeout(() => {
          if (CountStart) {
            setSecond(0);
            setNumber([]);
          }
        }, 500);
        return () => clearTimeout(secondtimer);
      }
    }
  }, [Second]);

  useEffect(() => {
    let number = Number;
    if (0.6 < Number % 1) {
      number -= 0.4;
      let convert = parseFloat(number).toFixed(2);
      setNumber(convert);
    }
    if (Second === 0) {
      setSecond(60);

      if (number == 0.01) {
        if (
          StartBreak &&
          props.dataofpomodoro[0].session / 2.0 != DataOfPomodoro[0].session
        ) {
          setTimeout(() => {
            setNumber(DataOfPomodoro[0].shortbreak);
            setBackground("bg4");
            set覺sshortbreak(true);
            setislongbreak(false);
            setSecond(60);
            setStartBreak(false);
          }, 5);
        } else if (
          StartBreak &&
          props.dataofpomodoro[0].session / 2.0 == DataOfPomodoro[0].session
        ) {
          setTimeout(() => {
            setNumber(DataOfPomodoro[0].longbreak);
            setBackground("bg4");
            set覺sshortbreak(false);
            setislongbreak(true);
            setSecond(60);
            setStartBreak(false);
          }, 5);
        } else {
          setNumber(StorageNumber);
          setSecond(60);
          setStartBreak(true);
        }

        const newdata = [...DataOfPomodoro];
        let item = { ...newdata[0] };
        const value = newdata[0].session;
        const newnumber = value - 1;
        item.session = newnumber;
        newdata[0] = item;
        setDataOfPomodoro([item]);
        if (newdata[0].session === -3) {
          setisFinished(true);
          setNumber([]);
          setSecond([]);
          setActiveSecond([]);
          alert("CONGRATULATIONS! " + "Your Stop Score:" + StopCounter);
          if (window.confirm) {
            window.location.reload();
            setNumber([]);
            setSecond([]);
          } else {
            window.location.reload();
            setNumber([]);
            setSecond([]);
            setActiveSecond([]);
          }
        }
      } else {
        let number = Number;
        number -= 0.01;

        let convert = parseFloat(number).toFixed(2);
        setNumber(convert);
      }
    }
  }, [Second]);
  useEffect(() => {
    if (Number.length == 0) {
    } else {
      if (
        DataOfPomodoro[0].shortbreak / 2.0 == Number &&
        !StartBreak &&
        isShortBreak
      ) {
        setBackground("bg3");
      } else if (
        DataOfPomodoro[0].longbreak / 2.0 == Number &&
        !StartBreak &&
        isLongBreak
      ) {
        setBackground("bg3");
      } else if (StorageNumber / 2.0 == Number && StartBreak) {
        setBackground("bg3");
      } else if (StorageNumber == Number && StartBreak) {
        setBackground("bg2");
      } else if (Number == 0.01 && isFinish) {
        setNumber([]);
        setSecond([]);
        setBackground("bg4");
      } else {
        setBackground("bg4");
      }
    }
  }, [Number, StartBreak, isLongBreak, isShortBreak]);
  const StartCount = (event) => {
    setBackground("bg2");
    setButtonClicked(true);
    setCountStart(true);
    setNumber(ActiveNumber);
    setStorageNumber(ActiveNumber);
    setSecond(ActiveSecond);
    activesetNumber([]);
  };
  const StopCount = (event) => {
    if (CountStart == true) {
      let number = StopCounter;
      setStopCounter((number += 1));
      setCountStart(false);
    }
  };
  const Continue = (event) => {
    setCountStart(true);
    let activesecond = Second;
    activesecond -= 1;
    setSecond(activesecond);
  };

  return (
    <div className={"myauto text-center " + Background}>
      <h1
        data-aos={"fade-right"}
        data-aos-duration="60000"
        className="text-muted"
      >
        {!StartBreak ? "...BREAK TIME..." : "...TIME TO WORK..."}
      </h1>

      <Card
        data-aos-duration="2000"
        data-aos={"flip-down"}
        style={{ border: "20px solid #F5FFFA", boxShadow: "5px 10px" }}
      >
        <CardContent>
          <small className="text-muted">
            {!ButtonClicked ? "SET YOUR TIME TO WORK (HOUR-MINUTE)" : ""}
          </small>

          <hr />
          <h1 className="h1">{ButtonClicked ? Number : ActiveNumber}</h1>

          <p>{Second}</p>

          <Input
            style={{ visibility: "visible" }}
            type="time"
            onChange={gettime}
            style={{ margin: "5px" }}
          />
          <hr />
          <CardActions
            style={{
              textAlign: "center",
              justifyContent: "center",
              alignItems: "center",
              margin: 0,
            }}
          >
            <Button
              style={{ textAlign: "center" }}
              disabled={ActiveNumber.length == 0}
              onClick={StartCount}
            >
              Start
            </Button>

            <Button
              disabled={
                (Number.length == 0) |
                (Number == 0) |
                (ActiveNumber.length !== 0)
              }
              onClick={StopCount}
            >
              Stop
            </Button>
            <Button onClick={Continue} disabled={CountStart}>
              Continue
            </Button>
            <hr />
          </CardActions>
        </CardContent>
      </Card>
    </div>
  );
}

export default Timer;
