import {
  Button,
  Card,
  CardActions,
  CardContent,
  FormControl,
  Input,
} from "@material-ui/core";
import AOS from "aos";
import "aos/dist/aos.css";
import React, { useEffect, useState } from "react";
import Timer from "./Time";
const LineForm = (props) => {
  const [DataOfPomodoro, setDataOfPomodoro] = useState([{}]);
  const [isnext, setIsnext] = useState(false);
  const [CreateForm, setCreateForm] = useState([]);
  const [CreateTimer, setCreateTimer] = useState([]);
  const [open, setOpen] = useState(false);
  const [FrontSide, setFrontSide] = useState(true);
  const handleForm = (event) => {
    event.preventDefault();

    const data = new FormData(event.target);
    var res1 = data.get("shortbreak").replace(":", ".");

    let convertnumber = parseFloat(res1).toFixed(2);

    var res2 = data.get("longbreak").replace(":", ".");
    let convertnumber2 = parseFloat(res2).toFixed(2);
    if ((convertnumber == 0.0) | (convertnumber2 == 0.0)) {
      alert("Short Break or Long Break cannot be 0.00  ");
      if (window.confirm) {
      } else {
      }
    } else if (convertnumber2 < convertnumber) {
      alert("Short Break cannot be higher than Long Break ");
      if (window.confirm) {
      } else {
      }
    } else if (convertnumber == convertnumber2) {
      alert("Short Break cannot be equal to Long Break ");
      if (window.confirm) {
      } else {
      }
    } else {
      setDataOfPomodoro([
        {
          session: parseInt(data.get("session")) * 4,
          shortbreak: convertnumber,
          longbreak: convertnumber2,
        },
      ]);
      setCreateForm([]);
      setIsnext(true);
      setCreateTimer([Timer]);
    }
  };
  const handleOpen = () => {
    setOpen(true);
    setFrontSide(false);
  };

  const handleClose = () => {
    setFrontSide(true);
  };

  const Createformfunction = (props) => {
    if (FrontSide) {
      return (
        <div className="myauto   text-center bgstart">
          <Card
            data-aos-duration="2000"
            data-aos={open ? "flip-left" : "zoom-in"}
            style={{
              border: "20px solid #F5FFFA",
              boxShadow: "5px 10px",
            }}
          >
            <CardContent>
              <h1 className="display-4 homeheadlines">FLEX POMODORO</h1>
              <hr />
              <form className="" method="post" onSubmit={handleForm}>
                <FormControl
                  style={{
                    flexWrap: "column",
                    flexDirection: "column",
                    display: "flex",
                  }}
                >
                  <span style={{ marginRight: "10px", marginTop: "6px" }}>
                    <span
                      style={{
                        border: "1px  solid",

                        color: "pink",
                      }}
                    ></span>
                    <small
                      style={{
                        marginLeft: "5px",
                        textAlign: "center",
                        color: "pink",
                        fontWeight: "bold",
                      }}
                    >
                      Pomodoro Session 1-4
                    </small>
                    <span
                      style={{
                        color: "pink",
                        marginLeft: "5px",
                        border: "1px solid",
                        borderRadius: "50px",
                      }}
                    ></span>
                  </span>

                  <Input
                    name="session"
                    type="number"
                    min="4"
                    inputProps={{ min: "1", max: "4", step: "1" }}
                    placeholder="Pomodoro session"
                    required
                  />
                  <span style={{ marginRight: "10px", marginTop: "10px" }}>
                    <span
                      style={{
                        border: "1px  solid",

                        color: "pink",
                      }}
                    ></span>
                    <small
                      style={{
                        marginLeft: "5px",
                        textAlign: "center",
                        color: "pink",
                        fontWeight: "bold",
                      }}
                    >
                      Short Break (HOUR-MINUTE)
                    </small>
                    <span
                      style={{
                        color: "pink",
                        marginLeft: "5px",
                        border: "1px solid",
                        borderRadius: "50px",
                      }}
                    ></span>
                  </span>

                  <Input
                    name="shortbreak"
                    type="time"
                    placeholder="shortbreak"
                    required
                  />
                  <span style={{ marginRight: "10px", marginTop: "10px" }}>
                    <span
                      style={{
                        border: "1px  solid",

                        color: "pink",
                      }}
                    ></span>
                    <small
                      style={{
                        marginLeft: "5px",
                        textAlign: "center",
                        color: "pink",
                        fontWeight: "bold",
                      }}
                    >
                      Long Break (HOUR-MINUTE)
                    </small>
                    <span
                      style={{
                        color: "pink",
                        marginLeft: "5px",
                        border: "1px solid",
                        borderRadius: "50px",
                      }}
                    ></span>
                  </span>

                  <Input
                    name="longbreak"
                    type="time"
                    placeholder="longbreak"
                    required
                  />

                  <CardActions>
                    <div className="row">
                      <div className="container col-12">
                        <Button
                          variant="contained"
                          style={{
                            background:
                              "linear-gradient(45deg,   rgba(249, 188, 186, 1) 5%,rgba(192, 230, 224, 1) 52%)",
                            textAlign: "center",
                            fontWeight: "bold",
                            fontSize: "10px",
                            color: "black",

                            zIndex: "1001",
                          }}
                          type="submit"
                        >
                          Start Pomodoro
                        </Button>
                      </div>
                    </div>
                  </CardActions>
                </FormControl>
              </form>
            </CardContent>
          </Card>
          <Card
            data-aos-duration="2000"
            data-aos={open ? "flip-left" : ""}
            style={{
              margin: "0px",
              position: "relative",
              border: "10px solid #F5FFFA",
              boxShadow: "5px 10px",
              borderRadius: "0",
            }}
          >
            <CardActions>
              <Button
                style={{
                  fontSize: "large",
                }}
                onClick={handleOpen}
              >
                <div className="how">How It Works</div>
              </Button>
            </CardActions>
          </Card>
          <Card
            data-aos-duration="2000"
            data-aos={open ? "flip-left" : ""}
            style={{
              margin: "10px",
              position: "relative",
              border: "10px solid #F5FFFA",
              boxShadow: "5px 10px",
              borderRadius: "0",
            }}
          >
            <CardActions>
              <p
                className="text-muted"
                style={{
                  marginTop: "1px !important",
                  padding: "0px !important",
                  fontSize: "15px",
                  fontWeight: "bold",
                }}
              >
                Created By{" "}
                <a href="https://github.com/Learnerest">@Learnerest</a>
              </p>
              <div className="text-muted">v/1.0</div>
            </CardActions>
          </Card>
        </div>
      );
    } else {
      return (
        <div className="myauto text-center bgstart">
          <Card data-aos-duration="2000" data-aos="flip-left">
            <CardContent>
              <div className="row p-4 text-center">
                <div
                  className="col-sm-12 pomodorosession"
                  style={{ maxWidth: 300, maxHeight: 500 }}
                >
                  <hr />
                  <h3 className="display-3 ">1 SESSION</h3>
                  <hr />

                  <div className="row text-center">
                    <p style={{ color: "green" }}>WORK</p>
                    <p>|</p>
                    <p style={{ color: "red" }}>SHORT BREAK</p>
                    <p>|</p>
                    <p style={{ color: "green" }}>WORK</p>
                    <p>|</p>
                    <p style={{ color: "red", fontWeight: "bold" }}>
                      LONG BREAK
                    </p>
                    <p>|</p> <p style={{ color: "green" }}>WORK</p>
                    <p>|</p>
                    <p style={{ color: "red" }}>SHORT BREAK</p>
                    <p>|</p>
                    <p style={{ color: "green" }}>WORK</p>
                  </div>
                </div>
              </div>
            </CardContent>
            <CardActions>
              <Button onClick={handleClose}>Go Back</Button>
            </CardActions>
          </Card>
        </div>
      );
    }
  };
  useEffect(() => {
    if (isnext) {
      setCreateForm([]);
    } else {
      AOS.init();
      setCreateForm([Createformfunction]);
    }
  }, [isnext, FrontSide]);

  if (!isnext) {
    return (
      <React.Fragment>
        {CreateForm.map((Element, index) => {
          return (
            <React.Fragment>
              <Element key={index}></Element>
            </React.Fragment>
          );
        })}
      </React.Fragment>
    );
  } else if (isnext) {
    return (
      <React.Fragment>
        {CreateTimer.map((Element, index) => {
          return (
            <Element key={index} dataofpomodoro={DataOfPomodoro}></Element>
          );
        })}
      </React.Fragment>
    );
  }
};

export default LineForm;
